@import url(https://fonts.googleapis.com/css?family=Oswald:500|Martel);
#homepage-vue-ap {
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  text-rendering: optimizeLegibility;
}
p {
  font-family: "Martel"
}
.tag-unit {
  text-align: center;
  margin-bottom: 1rem;
}
.entry-title, .page-title {
  font-family: 'Oswald';
  text-transform: uppercase;
  font-weight: normal;
}
.mh-excerpt-more {
  color: #BE1E2D
}
.mh-excerpt-more:hover {
  text-decoration: underline;
}
@media (min-width: 770px){
.home-grid {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.mh_magazine_custom_posts {
    width: 48%;
    margin: 0 1% 1rem;
}
.contest-splice {
    width: 100%;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
}

.featured-post {
  margin-bottom: 1rem;
}
.featured-post figure {
  height: 0;
  width: 100%;
  overflow: hidden;
  padding-bottom: 50%;
  position: relative;
  background: #222;
}
.featured-post figure img {
  width: 100%;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  position: absolute;
  top: 50%; left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.featured-post figure:hover img {
  -webkit-transform: scale(1.125) translateY(-45%);
          transform: scale(1.125) translateY(-45%);
  opacity: 0.8;
}
.featured-post figure figcaption {
  position: absolute;
  color: white;
  bottom: 0px;
  width: 100%;
  padding: 1.05rem;
  text-shadow: 0 0.125em 0.5em rgba(0,0,0.65);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)),to(rgba(0,0,0,0.85)));
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.85) 100%);
}
.featured-post h1 {
  font-family: 'Oswald';
  text-transform: uppercase;
  font-size: 2rem;
  line-height: 1em;
  margin-bottom: 0.25em;
  font-weight: normal;
  letter-spacing: -0.025em;
}
.featured-post figure figcaption h1 a {
  color: white;
}
.featured-post-excerpt {
  background: #F7F7F7;
  padding: 1rem;
}
@media (min-width: 770px){
.featured-post-excerpt {
    padding: 1.05rem;
}
.featured-post-excerpt p {
    font-size: 1.05rem;
}
.featured-post h1 {
    font-size: 3rem;
}
}



















.contest-splice {
  margin: 1rem 0;
  padding: 0 0.5rem;
}
.contest-splice figure {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
  -webkit-box-shadow: 0 0.125rem 0.5rem rgba(0,0,0,0.25);
          box-shadow: 0 0.125rem 0.5rem rgba(0,0,0,0.25);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.contest-splice figure:hover {
  -webkit-box-shadow: 0 0.25rem 0.6rem rgba(0,0,0,0.3);
          box-shadow: 0 0.25rem 0.6rem rgba(0,0,0,0.3);
  -webkit-transform: translateY(-0.125rem);
          transform: translateY(-0.125rem);
}
.contest-splice figure .contest-splice-content {
  margin-top: 1rem;
}
.contest-splice h1 {
  font-family: 'Oswald';
  text-transform: uppercase;
  font-size: 2rem;
  line-height: 1em;
  margin-bottom: 0.25em;
  font-weight: normal;
  letter-spacing: -0.025em;
}
.contest-splice-badge {
  font-size: 80%;
  display: inline-block;
  color: #fff;
  padding: 0.625em 0.9375em;
  margin-bottom: 1rem;
  background: #be1e2d;
  font-weight: bold;
}
@media (min-width: 650px){
.contest-splice figure {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.contest-splice figure .contest-splice-image {
    width: 33.33333%;
}
.contest-splice figure .contest-splice-content {
    margin-top: 0;
    margin-left: 1rem;
    width: 66.6666%;
}
}

.action-button {
  border: 0;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none; /* for button */
  -moz-user-select: none;
  -ms-user-select: none;
  display: block;
  width: 100%;
  /* height: 3rem; */
  /* font-size: 1rem; */
  /* background: #DDD; */
  border: 2px solid #2a2a2a;
  font-weight: bold;
  text-align: center;
  color: #2a2a2a;
  background: transparent;
  text-transform: uppercase;
  padding: 10px 15px;
  font-size: 0.875rem;
}
.action-button:hover {
  border-color: #BE1E2D;
  color: #BE1E2D;
}
.action-button:focus {
  outline: none;
}

h3.mh-custom-posts-xl-title {
  font-family: 'Oswald';
